html, body {
  height: 100%;
}

body {
  margin: 0;
  background-color: black;
}

html {
  font-family: sans-serif;
  font-size: 14pt;
}

#root {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100%;
}
.mobileActions {
  display: none;
}

/*@media (min-width: 750px) {*/
.mobileActions {
  display: -webkit-flex;
  display: flex;
  margin-left: auto;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.mobileActions a {
  font-size: 18pt;
  color: white;
  padding-left: 25px;
  padding-top: 3%;
}
/*}*/

header {
  padding: 1% 6%;
  background: black;
  color: #f1f1f1;
  display: -webkit-flex;
  display: flex;
}

#logo {
  height: 60px;
}

.main p::-moz-selection {
  background: aqua; /* WebKit/Blink Browsers */
}
.main p::selection {
  background: aqua; /* WebKit/Blink Browsers */
}
.main p::-moz-selection {
  background: aqua; /* Gecko Browsers */
}

div.main a {
  color: teal;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  border-bottom: 2px dotted teal;
}

div.main a:hover {
  background-color: aqua;
}

div.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 90%;
  min-height: 100%;
  padding: 10px;
  -webkit-align-self: center;
          align-self: center;
  background-color: white;
}

.main article {
  /*flex: 1;*/
  padding-left: 20px;
  padding-right: 20px;
  min-width: 200px;
  /*flex-direction: column;*/
  width: 90%;
  /*margin-left: auto;*/
  /*margin-right: auto;*/
  /*padding-top: 2%;*/
  line-height: 1.3em;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.main article.announcements {
  width: 30%;
  max-width: 350px;
  -webkit-flex-grow: 3;
          flex-grow: 3;
  overflow: hidden;
}

article p.pinned {
  padding: 15px;
  background-color: bisque;
  line-height: 1.5em;
}

.main article.manifesto {
  width: 60%;
  min-width: 300px;
}

div.pre-footer {
    margin-top: 5%;
    margin-bottom: 1%;
    font-size: 10pt;
    text-align: center;
    line-height: 3em;
    letter-spacing: 1pt;
}

div.sponsors {
    /*font-weight: bold;*/
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
}

div.sponsors > img {
    /*position: relative;*/
    /*top: 50%;*/
    /*transform: translateY(-50%);*/
    margin-right: 10px;
    margin-left: 10px;
    /*margin: auto 50px;*/
    width: 20%;
}

div.footer {
  height: 100px;
  padding-top: 30px;
  text-align: center;
  color: white;
  line-height: 2em;
}

div.footer > .quote {
  display: none;
}

@media (min-width: 650px) {
  div.footer > .quote {
    display: block;
    line-height: 3em;
  }
}

